body,
html {
    .resetmp;
    font-family: @primary-font;

    a,
    button {
        text-decoration: none !important;
        border: none;
        outline: none !important;

        &:focus {
            border: none;
            outline: none;
        }
    }

    main {
        .simplebar-scrollbar {
            width: 10px;

            &::before {
                background: @primary-color-dark;
            }
        }
    }
}